.person_profile_wrapper {
  // colors
  &.startup {
    --color-btn: rgba(253, 89, 1, 1);
    --color-btn-01: rgba(253, 89, 1, 0.1);
    --color-btn-08: rgba(253, 89, 1, 0.8);
    --color-card-shadow: rgba(234, 238, 242, 0.4);
    --color-text: white;
    --color-outline-btn: rgba(253, 89, 1, 1);
    border-radius: var(--border-radius);

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
      margin: 0;
    }

    a {
      text-decoration: none;
      color: inherit;
    }
  }

  &.investor {
    --color-btn: rgba(211, 243, 107, 1);
    --color-btn-01: rgba(211, 243, 107, 0.1);
    --color-btn-08: rgba(211, 243, 107, 0.8);
    --color-card-shadow: rgba(211, 243, 107, 0.1);
    --color-text: black;
    --color-outline-btn: black;
    --border-radius: 20px;

    border-radius: var(--border-radius);
    border: 1px solid rgba(0, 0, 0, 0.1);

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
      margin: 0;
    }

    a {
      text-decoration: none;
      color: inherit;
    }
  }
}

.person_profile_wrapper {
  .btn-capital {
    width: fit-content;
    background-color: var(--color-btn);
    border: 1px solid var(--color-btn);
    border-radius: 5px;
    outline: none;

    color: var(--color-text);
    font-family: "Poppins", sans-serif;
    font-size: 1rem;
    line-height: 1rem;

    font-weight: 400;
    padding: 0.75em 1em;

    cursor: pointer;
    transition: all 150ms ease-in-out;

    &:hover {
      transform: scale(0.95);
    }
  }

  .btn-capital-small {
    width: fit-content;

    background-color: var(--color-btn);
    border: 1px solid var(--color-btn);
    border-radius: 5px;
    outline: none;

    color: var(--color-text);
    font-family: "Poppins", sans-serif;
    font-size: 0.75rem;
    line-height: 0.5rem;

    font-weight: 500;
    padding: 0.5em 1.5em;

    cursor: pointer;
    transition: all 150ms ease-in-out;

    &:hover {
      transform: scale(0.95);
    }
  }

  .btn-capital-outline {
    width: fit-content;

    background-color: white;
    border: 1px solid var(--color-btn);
    border-radius: 5px;
    outline: none;

    color: var(--color-outline-btn);
    font-family: "Poppins", sans-serif;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    padding: 0.75em 1.5em;

    cursor: pointer;
    transition: all 150ms ease-in-out;

    &:hover {
      transform: scale(0.95);
    }
  }

  .div__heading {
    text-decoration: underline;
    text-decoration-color: var(--color-btn);
    text-decoration-thickness: 2px;
    text-underline-offset: 0.25em;
  }

  .main__heading {
    font-size: 1.5rem;
    font-weight: 500;
  }
}
